.breadcrumb {
  background: transparent;
  align-items: center;
  margin: 0 0 1.25rem;
  padding: 0;
  h1 {
    font-size: 1.5rem;
    line-height: 1;
    // padding-right: .5rem;
    margin: 0;
    // margin-right: 0.5rem;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      display: inline-block;
      position: relative;
      padding: 0 0.5rem;
      line-height: 1;
      vertical-align: bottom;
      color: $gray-600;
      &:after {
        right: 0;
        width: 1px;
        content: "";
        height: 100%;
        position: absolute;
        background: $gray-600;
        border-radius: 5px;
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
      a {
        color: $heading;
      }
    }
  }
}

[dir="rtl"] {
  .breadcrumb {
    h1 {
      font-size: 1.5rem;
      line-height: 1;
      // padding-right: .5rem;
      margin: 0;
      margin-left: 0.5rem;
    }
  }
}

@media (max-width: 576px) {
  .breadcrumb {
    flex-direction: column;
    align-items: flex-start;
    ul li:first-child {
      padding-left: 0;
    }
  }
}
