html [type="button"],
.btn[type="button"] {
  -webkit-appearance: none !important;
}

.btn {
  &.rounded,
  &.btn-rounded {
    border-radius: 40px !important;
  }
  &.btn-xl {
    padding: 0.75rem 2rem;
    font-size: 1.18rem;
  }
  // padding: 0.5rem 1.25rem;
  &:focus {
    box-shadow: none;
  }
}

.btn-secondary,
.btn-raised-secondary {
  color: $foreground !important;
  background-color: $background !important;
}

.btn-icon {
  [class^="i-"],
  .icon {
    vertical-align: middle;
    margin: 0 2px;
    -webkit-font-smoothing: subpixel-antialiased;
  }
  &.rounded-circle {
    width: 44px;
    height: 44px;
    padding: 0;
  }
}

.btn-icon-text {
  [class^="i-"],
  .icon {
    vertical-align: middle;
    margin: 0 2px;
    -webkit-font-smoothing: subpixel-antialiased;
  }
}

.btn-outline-email {
  background: rgba($gray-200, 0.6);
}

.btn-spinner {
  width: 1em;
  height: 1em;
  background: transparent;
  border-radius: 50%;
  margin: 0 16px 0 0;
  border: 2px solid transparent;
}

.btn-checkbox {
  .checkbox {
    display: inline;
  }
}

.btn.btn-outline-light.btn-svg {
  border-color: $gray-700;
  &.active,
  &:hover {
    background: $primary;
    border-color: $primary;
    svg {
      fill: #ffffff;
    }
  }
  &:focus {
    box-shadow: none !important;
  }
}

.btn-raised {
  color: #fff;
}

@each $name, $value in $theme-colors {
  .btn-#{$name},
  .btn-outline-#{$name} {
    border-color: $value;
    .btn-spinner {
      animation: btn-glow-#{$name} 1s ease infinite;
    }
    &:hover {
      background: $value;
      box-shadow: 0 8px 25px -8px $value;
      border-color: $value;
    }
    &:focus {
      box-shadow: none;
      box-shadow: 0 8px 25px -8px $value;
    }
  }
  .btn-raised.btn-raised-#{$name} {
    background: $value;
    box-shadow: 0 4px 6px rgba($value, 0.11), 0 1px 3px rgba($value, 0.08);
  }
  @keyframes btn-glow-#{$name} {
    0% {
      box-shadow: 0 0 0 0.4em darken($value, 10%), 0 0 0 0.1em darken($value, 10%);
      transform: rotate(360deg);
    }
    50% {
      border-top-color: #ffffff;
    }
    100% {
      box-shadow: 0 0 0 0.4em darken($value, 10%), 0 0 0 3.6em transparent;
    }
  }
}

