.jobs-container {
    display: flex;
    align-items: center;
    position: relative;
    margin: 20px 0;
}

.jobs-row {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
    gap: 10px;
    padding: 10px;
    margin: 0 24px;
    /* Adjust margin for better fit */
    width: calc(100% - 48px);
    /* Adjust the width to fit the buttons */
}

.scroll-btn-left,
.scroll-btn-right {
    background-color: #c9d1da;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    font-size: 20px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
}

.scroll-btn-left {
    left: 0;
}

.scroll-btn-right {
    right: 0;
}

.scroll-btn-left:hover,
.scroll-btn-right:hover {
    background-color: #8f00b3;
    /* Darker color on hover */
}

/* Adjust button size and appearance if needed */
.scroll-btn-left,
.scroll-btn-right {
    width: 40px;
    /* Button width */
    height: 40px;
    /* Button height */
    border-radius: 50%;
    /* Make the buttons circular */
}

.browse-job-card {
    transition: box-shadow 0.3s ease-in-out;
    border-radius: 10px;
    /* Optional: To make the card edges rounded */
}

.browse-job-card:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    /* Add shadow on hover */
    border: 1px solid #ccc;
    /* Optional: You can change the border color on hover */
}

@media (max-width: 1200px) {
    .browse-job-card {
        width: 250px;
    }

    .browse-job-card-img {
        width: 500px;
    }
}

@media (max-width: 768px) {
    .browse-job-card {
        width: 900px;
        height: 500px;
    }

    .browse-job-card-img {
        width: 900px;
        height: 900px;
    }

    .browse-job-card .card-title {
        font-size: 1em;
    }

    .browse-job-card .card-subtitle,
    .browse-job-card .card-text {
        font-size: 0.85em;
    }
}

@media (max-width: 576px) {
    .browse-job-card {
        width: 500px;
        height: 300px;
    }

    .browse-job-card-img {
        width: 500px;
        height: 400px;
    }

    .browse-job-card .card-title {
        font-size: 0.9em;
    }

    .browse-job-card .card-subtitle,
    .browse-job-card .card-text {
        font-size: 0.75em;
    }
}



