  }
  
  .cta-container1 {
    display: flex;
    flex-direction: column; /* Stack the content vertically */
    align-items: center; /* Center the content horizontally */
    text-align: center;
    padding: 20px;
  }
  
  .cta-content {
    margin-bottom: 20px; /* Space between the content and the button */
  }
  
  .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Ensure the container takes the full width */
  }
  
  .thq-button-filled.cta-button {
    font-size: 16px;
    padding: 10px 20px;
    transition: all 0.3s ease;
    max-width: 500px;
    width: 100%; /* Make the button responsive */
  }
  
  /* Responsive styles for tablet screens */
  @media screen and (max-width: 768px) {
    .thq-button-filled.cta-button {
      font-size: 12px;
      padding: 8px 16px;
    }
  }
  @media screen and (max-width: 570px) {
    .thq-button-filled.cta-button {
      font-size: 10px;
      padding: 3px 10px;
      max-width: 300px;
    }
  }
  @media screen and (max-width: 525px) {
    .thq-button-filled.cta-button {
      font-size: 8px;
      padding: 3px 10px;
      max-width: 300px;
    }
  }
  /* Responsive styles for mobile screens */
  @media screen and (max-width: 480px) {
    .cta-container1 {
      padding: 10px;
    }
  
    .thq-button-filled.cta-button {
      font-size: 8px;
      padding: 2px 8px;
      margin-right: 0;
      max-width: 250px;
    }
  }
  

.responsive-button {
    width: 100%;
    /* Full width on smaller screens */
    max-width: 400px;
    /* Limit the button's max width */
}

/* Media queries to adjust the button width based on screen size */
@media (min-width: 768px) {
    .responsive-button {
        width: 80%;
        /* 80% width on medium screens */
    }
}

@media (min-width: 1024px) {
    .responsive-button {
        width: 60%;
        /* 60% width on larger screens */
    }
}
.home-search-bar {
    position: relative;
    width: 50%;
    left: 300px;
    display: flex;
    align-items: center;
  }
  
  .search-input-bar {
    width: 100%;
    height: 50px;
    padding: 10px 20px;
    padding-right: 200px;
    border: 2px solid #ccc;
    border-radius: 8px;
    font-size: 16px;
    transition: all 0.3s ease;
  }
  
  .search-input-bar::placeholder {
    color: #aaa;
  }
  
  .search-button {
    height: 40px;
    width: 130px;
    font-size: 18px;
    transition: all 0.3s ease;
    margin-right: 5px;
  }
  
  .fa-search-icon {
    position: absolute;
    top: 12px;
    right: 150px;
    transition: all 0.3s ease;
  }
  
  /* Responsive styles */
  @media screen and (max-width: 768px) {
    .home-search-bar {
      width: 80%;
      left: 0;
      margin: 0 auto;
    }
  
    .search-input-bar {
      height: 45px;
      font-size: 14px;
      padding-right: 130px;
    }
  
    .fa-search-icon {
      top: 10px;
      right: 110px;
      font-size: 16px;
    }
  
    .search-button {
      width: 100px;
      height: 28px;
      font-size: 16px;
    }
  }
  
  @media screen and (max-width: 480px) {
    .home-search-bar {
      width: 100%;
      left: 0;
      margin: 0 auto;
    }
  
    .search-input-bar {
      height: 45px;
