//opacity round
.badge-round {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  line-height: 28px;
  font-size: 1.2rem;
}
@each $name, $value in $theme-colors {
  .badge-round.badge-round-opacity-#{$name} {
    background: lighten($value, 40%) !important;
    color: $value !important;
  }
}
// end-opacity-round

// begin square opacity

.badge-square {
  border-radius: 20%;
  width: 40px;
  height: 40px;
  line-height: 30px;
  font-size: 1.2rem;
}
@each $name, $value in $theme-colors {
  .badge-square.badge-square-opacity-#{$name} {
    background: lighten($value, 40%);
    color: $value;
  }
}
.w-badge {
  border-radius: 0;
  padding: 4px;
  color: #fff;
}
.r-badge {
  padding: 4px;
}

// end square opacity
