.image-slider-container {
    display: flex;
    position: relative;
    width: 100%; /* Full width of the container */
    height: 200px; /* Set a fixed height for the container */
}

.image-slider {
    display: flex;
    position: relative;
    top: 0; /* Align images at the top */
    height: 100%; /* Ensure it takes full height of the container */
    white-space: nowrap; /* Prevent line breaks */
    animation: slide-left-to-right 70s linear infinite; /* Animate left to right */
}

.image-wrapper {
    display: inline-flex; /* Change to inline-flex for better alignment */
    align-items: center;
    justify-content: center;
    width: 250px; /* Set fixed width for each image wrapper */
    height: 150px; /* Set fixed height for each image wrapper to maintain square shape */
    margin: 0 10px; /* Adjust spacing between images */
    overflow: hidden; /* Hide overflow to ensure images do not exceed the wrapper */
}

.square-image {
    width: 100%; /* Use full width of the wrapper */
    height: 100%; /* Use full height of the wrapper */
    object-fit: cover; /* Ensure the image covers the square without distortion */
}

@keyframes slide-left-to-right {
    0% {
        transform: translateX(-50%); /* Start at the initial position */
    }
    100% {
        transform: translateX(0); /* Move left */
    }
}


